<template>
    <div>
        <form class="form-signin mt-5" @submit.prevent="login">
            <h1 class="h3 mb-3 font-weight-normal text-center">登入</h1>
            <label for="inputEmail" class="sr-only">Email address</label>
            <input type="email" id="inputEmail" class="form-control"
                placeholder="電子郵件" v-model="user.username" required autofocus>
            <label for="inputPassword" class="sr-only">Password</label>
            <input type="password" id="inputPassword"
                class="form-control" v-model="user.password" placeholder="密碼" required>
            <div class="checkbox mb-3">
                <label>
                <input type="checkbox" value="remember-me"> 記住帳號
                </label>
            </div>
            <button class="btn btn-lg btn-desert btn-block" type="submit">登入</button>
        </form>
    </div>
</template>

<script>
export default {
    data(){
        return{
            user:{
                username:'',
                password:'',
            }
        }
    },
    methods:{
        login(){
            const vm = this
            const api = `${process.env.VUE_APP_APIPATH}/admin/signin`
            vm.$http.post(api,vm.user).then((response) => {
                console.log(response.data)
                if(response.data.success) {
                    vm.$router.push('/admin/products');
                }   
            })
        },
    }
}
</script>

